import './style.css'
import cameraData from './camera-data.json'
import * as THREE from 'three'
import * as CONTROLS from 'three-story-controls'
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader.js'
import { AnimationClip } from 'three'
import Swal from 'sweetalert2'

const loading = document.querySelector('.loading')

const gltfLoader = new GLTFLoader()

/**
 * Base
 */
// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()


//Models

gltfLoader.load(
    './models/store2/store.glb',
    (gltf) =>
    {
        console.log('progress')
        loading.style.display = "none"
        gltf.scene.scale.set(1,1,1)
        scene.add(gltf.scene)
    },
    () =>{
        console.log('success')
        loading.style.display = "block"
    }
    
)

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.set(0, 20, 20)
scene.add(camera)

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.physicallyCorrectLights = true
renderer.outputEncoding = THREE.sRGBEncoding
renderer.toneMapping = THREE.ACESFilmicToneMapping
renderer.toneMappingExposure = 0.7
renderer.shadowMap.type = THREE.PCFSoftShadowMap

const pois = cameraData.pois.map((item, index) => {
    return {
      frame: index * 6,
      caption: `This is caption for point #${index + 1}`,
    }
  })

//Camera Rig
 const rig = new CONTROLS.CameraRig(camera,scene)
 const clip = AnimationClip.parse(cameraData.animationClip)
rig.setAnimationClip(clip)
rig.setAnimationTime(0)

//const scrollElement = document.querySelector('.scroller')

//   const controls = new CONTROLS.FreeMovementControls(rig)
// const controls = new CONTROLS.ScrollControls(rig,{
//     scrollElement
// })

const controls = new CONTROLS.PathPointsControls(rig, pois)


const controls3dof = new CONTROLS.ThreeDOFControls(rig, {
    panFactor: Math.PI / 10,
    tiltFactor: Math.PI / 10,
    truckFactor: 0,
    pedestalFactor: 0,
  })


controls.enable()
//controls3dof.enable()

const grid = new THREE.GridHelper(150, 50)
grid.position.set(0, 0, 0)
// scene.add(grid)

//const cameraHelper = new CONTROLS.CameraHelper(rig, controls, renderer.domElement)


function render(t){
    if(rig.hasAnimation){
        controls.update(t)
        controls3dof.update(t)
    }
    renderer.render(scene, camera)
    //cameraHelper.update(t)
    window.requestAnimationFrame(render)
}

render()




